<template>
  <div>
    <ul class="notice">
      <li>
        <svg class="icon icon1" aria-hidden="true">
          <use xlink:href="#icon-gonggaolan"></use>
        </svg>
        <span class="notice_text">公告栏</span>
      </li>
      <li class="line">
        <svg class="icon icon2" aria-hidden="true">
          <use xlink:href="#icon-xiantiao"></use>
        </svg>
      </li>
    </ul>
    <van-pull-refresh v-model="isLoading" @refresh="getNewsList">
      <van-list
        class="list"
        v-model="loading"
        @load="getNewsList"
        :finished="finished"
      >
        <van-cell
          is-link
          class="van-multi-ellipsis--l3"
          v-for="(item, index) in noticeList"
          :key="index"
          :label="item.time"
          @click="handleToDetail(item.id)"
        >
          <template #title>
            <span class="custom-title">{{ item.title }}</span>
            <svg v-if="index < 2" class="icon icon1" aria-hidden="true">
              <use xlink:href="#icon-NEW"></use>
            </svg>
          </template>
        </van-cell>
      </van-list>
      <van-pagination
        class="pagination"
        v-model="currentPage"
        @change="getNoticeList"
        :page-count="pageNum"
        mode="simple"
      />
    </van-pull-refresh>
  </div>
</template>

<script>
import '../../../public/assets/iconfont'

export default {
  name: 'NoticeList',
  data() {
    return {
      noticeList: [],
      loading: false,
      isLoading: false,
      finished: false,
      currentPage: 1,
      pageNum: 1
    }
  },
  methods: {
    async getNoticeList() {
      let res = await this.$http.get('/api/home/announces')
      if (res.status !== 200) return false
      if (res.data.code != 200) return false
      this.noticeList = res.data.data.list
      this.pageNum = res.data.data.pageNum
      this.loading = false
      this.isLoading = false
      this.finished = true
    },
    handleToDetail(id) {
      this.$router.push('/noticeList/detail/' + id)
    }
  },
  mounted() {
    this.getNoticeList()
  }
}
</script>

<style scoped>
.pagination {
  margin-top: 1.25rem;
}
.icon1 {
  padding-top: 0.5rem;
  width: 1.25rem;
  height: 1.25rem;
}
.icon2 {
  width: 7.5rem;
  height: 0.5rem;
  fill: #f9ca4f;
}
.notice {
  margin-left: 0.5rem;
  font-size: 1.25rem;
}
.notice_text {
  margin-left: 0.375rem;
}
.line {
  font-size: 0;
}
.list {
  margin-top: 1.25rem;
  min-height: 32.25rem;
}
.custom-title {
  font-size: 1rem;
}
</style>
<style>
.van-cell__right-icon {
  margin-top: 0.5rem;
}
</style>
